import { Component, OnInit } from '@angular/core';
import { ViewChild } from '@angular/core';
import { Chart } from 'chart.js';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-bank',
  templateUrl: './bank.component.html',
  styleUrls: ['./bank.component.css']
})
export class BankComponent implements OnInit {
  @ViewChild('barCanvas') barCanvas;

  barChart: any;
  bloodData: any = [];

  constructor(private http: HttpClient) { }
  chartFunc(bloodData) {
    this.barChart = new Chart(this.barCanvas.nativeElement, {

      type: 'bar',
      data: {
        labels: ["A+", "A-", "AB+", "AB-", "B+", "B-", "O+", "O-"],
        datasets: [{
          label: 'Litres of blood',
          data: bloodData,//[12, 19, 3, 5, 2, 3, 8, 6],
          backgroundColor: [
            'rgba(255, 99, 132, 0.2)',
            'rgba(54, 162, 235, 0.2)',
            'rgba(255, 206, 86, 0.2)',
            'rgba(75, 192, 192, 0.2)',
            'rgba(153, 102, 255, 0.2)',
            'rgba(255, 159, 64, 0.2)',
            'rgba(100, 140, 76, 0.2)',
            'rgba(100, 68, 76, 0.2)'
          ],
          borderColor: [
            'rgba(255,99,132,1)',
            'rgba(54, 162, 235, 1)',
            'rgba(255, 206, 86, 1)',
            'rgba(75, 192, 192, 1)',
            'rgba(153, 102, 255, 1)',
            'rgba(255, 159, 64, 1)',
            'rgba(100, 140, 76, 1)',
            'rgba(100, 68, 76, 1)'
          ],
          borderWidth: 1
        }]
      },
      options: {
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true
            }
          }]
        }
      }

    });
  }
  ngOnInit() {
    this.http.get('/getBlood')
      .subscribe(
        data => {
          var resData = JSON.parse(JSON.stringify(data));
          for (var i = 0; i < resData.msg.length; i++) {
            console.log(resData.msg[i].blood.amount);
            this.bloodData.push(resData.msg[i].blood.amount);
            if(i==resData.msg.length-1){
              console.log(this.bloodData);
              this.chartFunc(this.bloodData);
            }
          }
        }
      )
  }
}


