import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {
  browse: string = "Browse";
  feedback : any = "";

  constructor(private http: HttpClient) { }
  onFileChange(event) {
    let reader = new FileReader();
    let reader1 = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      let file = event.target.files[0];
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.browse = file.name;
        if (this.browse == "")
          this.browse = "Browse";
      }
      reader1.readAsText(file);
      reader1.onload = (e) => {
        var lines = reader1.result.split(/\r\n|\n|\r/);
        var result = [];
        var headers = lines[0].split(",");
        for (var i = 1; i < lines.length; i++) {
          var obj = {};
          var currentline = lines[i].split(",");
          for (var j = 0; j < headers.length; j++) {
            obj[headers[j]] = currentline[j];
          }
          result.push(obj);
        }
        console.log(result);
        this.http.post('/addDonors', result)
          .subscribe(
            data => {
              console.log(data);
              var resData = JSON.parse(JSON.stringify(data));
              this.feedback = resData.msg;
              var x = document.getElementById("snackbar");
              x.className = "show";
              setTimeout(function () { x.className = x.className.replace("show", ""); }, 3000);
            }
          )
      }
    }
  }

  ngOnInit() {
  }

}
